.answers-options {
    display: grid;
    grid-template-rows: repeat(6, 1fr);
    border: 1px solid var(--border-color);
    background-color: var(--background-color-secondary);
    border-radius: 5px;
    height: 320px;

    .answers-options__item {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 13.6px 20px;
        border-bottom: 1px solid var(--border-color);
        cursor: pointer;

        &:hover {
            background-color: var(--color-light);
        }

        &:last-child {
            border-bottom: none;
        }

        .answers-options__status {
            height: 10px;
            width: 10px;
            background-color: var(--color-light);
            border-radius: 50%;
            margin-right: 15px;
            pointer-events: none;
        }

        .answers-options__title {
            pointer-events: none;
        }

        .answers-options__status_right {
            background-color: var(--quiz-color-active);
        }

        .answers-options__status_wrong {
            background-color: var(--quiz-color-wrong);
        }
    }
}