
.audio {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.audio__btn-circle {
    width: 40px;
    height: 40px;
    border: 2px solid var(--quiz-color-active);
    border-radius: 50%;
    position: relative;

    .fa-play,
    .fa-pause {
        position: absolute;
        top: 8px;
        left: 12px;
        font-size: 1.2rem;
        color: var(--quiz-color-active);
        pointer-events: none;
    }

    &:hover {
        border: 2px solid var(--quiz-color-active-hover);
        color: var(--quiz-color-active-hover);
        cursor: pointer;
    }
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 6px;
    background: var(--border-color);
    border-radius: 5px;
    position: relative;
	display: inherit;
	border-radius: inherit;
	margin: 0;
    border: none;
    outline: none;
    margin-top: 10px;
    touch-action: none;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: var(--extra-light-color);
    cursor: pointer;
}

.change-volume {
    position: absolute;
    top: -90px;
    left: 8px;
    width: 30px;
    height: 90px;
    background-color: var(--background-color-secondary);
    border-radius: 5px;
    border: 1px solid var(--border-color);
    z-index: 3;
}

.slider_vertical {
    position: absolute;
    top: 31px;
    left: -24px;
    transform: rotate(270deg);
    width: 75px;
}

.audio__input {
    padding: 2px;
    margin-left: 20px;
    border-radius: 9999em;
    flex-grow: 1;
    
    .audio__time-info {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .audio__time {
            font-size: 15px;
            color: var(--audio-time);
            margin-top: 8px;
        }
    }
}

.audio__volume {
    width: 40px;
    height: 40px;
    position: relative;
    cursor: pointer;

    .fa-volume-high,
    .fa-volume-low,
    .fa-volume-xmark {
        position: absolute;
        color: var(--quiz-color-active);
        font-size: 1.2rem;
        top: 5px;
        left: 12px;
        pointer-events: none;
    }
}